.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.contacts {
  background-image: linear-gradient(180deg, rgb(255, 255, 255) 5%, rgb(255, 245, 246) 4%);
  @media screen and (min-width: 1200px) {
    background-image: url(../images/sectionbg2_1.png),
      linear-gradient(180deg, rgb(255, 255, 255) 5%, rgb(255, 245, 246) 4%);
    background-position: center 0;
    background-repeat: no-repeat;
    // position: absolute;
  }
}

