.contacts__card {
  @media screen and (min-width: 768px) {
    max-width: 720px;
    display: flex;
  }
  @media screen and (min-width: 1200px) {
    margin: auto;
    justify-content: center;
  }
}

.contacts__text {
  font-weight: 500;
  font-size: 12px;
  padding: 43px 21px 43px 20px;
  width: 220px;
  height: 427px;

  background: $white-color;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;

  margin-inline: auto;
  @media screen and (min-width: 768px) {
    max-width: 720px;
    height: 544px;
    padding: 50px 32px 50px 31px;
  }
  @media screen and (min-width: 1200px) {
    max-width: 1080px;
    width: 330px;
    height: 572px;
    padding: 51px 44px 62px 43px;
  }
}

.contacts__text:not(:last-child) {
  @media screen and (max-width: 767px) {
    margin-bottom: 20px;
  }
  @media screen and (min-width: 768px) {
    margin-right: 20px;
  }
  @media screen and (min-width: 1200px) {
    margin-right: 31px;
  }
}

.contacts__name {
  width: 80px;
  height: 16px;
  align-items: center;

  color: $white-color;
  background: $heading-back-color-contacts-one;
  border-radius: 4px;
  margin-top: 0;
  display: flex;
  justify-content: center;

  @media screen and (max-width: 767px) {
    margin-bottom: 25px;
  }
  @media screen and (min-width: 768px) {
    width: 73px;
    height: 28px;
    font-size: 16px;
    margin-bottom: 30px;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 32px;
  }
}

.contacts__name_text {
  width: 130px;
  height: 16px;
  align-items: center;

  color: $white-color;
  background: $heading-back-color-contacts-two;
  border-radius: 4px;
  margin-top: 0;
  display: flex;
  justify-content: center;
  @media screen and (max-width: 767px) {
    margin-bottom: 25px;
  }
  @media screen and (min-width: 768px) {
    width: 138px;
    height: 28px;
    font-size: 16px;
    margin-bottom: 30px;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 32px;
  }
}

.contacts_city {
  line-height: 186%;

  margin-top: 0;
  margin-bottom: 10px;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    margin-bottom: 14px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
    margin-bottom: 10px;
  }
}

.contacts_city__text {
  line-height: 1.5;

  color: $primary-text-color;

  margin-top: 0;
  padding-bottom: 25px;
  border-bottom: 1px solid #e1e1e1;
  margin-bottom: 25px;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    margin-bottom: 30px;
    line-height: 1.66;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
    padding-bottom: 32px;
    margin-bottom: 32px;
    line-height: 1.83;
  }
}
.contacts_city__day {
  margin: 0;
  line-height: 186%;
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
  }
}

.contacts_city__time {
  margin: 0;
  line-height: 186%;
  color: $text-color;
  margin-bottom: 10px;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    margin-bottom: 16px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 15px;
    margin-bottom: 16px;
  }
}

.contacts_city__time-output {
  margin: 0;
  line-height: 186%;
  color: $text-color;
  padding-bottom: 25px;
  border-bottom: 1px solid #e1e1e1;
  margin-bottom: 25px;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    padding-bottom: 30px;
    margin-bottom: 30px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 15px;
    padding-bottom: 32px;
    margin-bottom: 32px;
  }
}

.contacts_city__tell {
  line-height: 1.5;
  color: $primary-text-color;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.83;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
  }
}
.contacts_city__mail {
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
  }
  line-height: 18px;
  color: $primary-text-color;
}

.button {
  font-family: inherit;
  font-size: inherit;
  border: none;
  background-color: transparent;
  color: inherit;
  cursor: pointer;
}

.contacts__btn {
  margin-top: 54px;
  text-align: center;
  @media screen and (min-width: 768px) {
    margin-top: 81px;
  }
}

.contacts__btn {
  @media screen and (min-width: 768px) {
    max-width: 720px;
    display: flex;
    justify-content: center;
  }
  @media screen and (min-width: 1200px) {
    margin-inline: auto;
  }
}

.contacts__btn_item {
  @media screen and (min-width: 768px) {
    padding-right: 14.5px;
  }
  @media screen and (min-width: 1200px) {
    padding-right: 15px;
  }
}

.contacts_btn__offers {
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;

  background-color: $button-and-heading-color;
  color: $white-color;

  width: 161.45px;
  height: 44px;
  border-radius: 22px;

  margin-bottom: 10px;
  @media screen and (min-width: 768px) {
    width: 191.45px;
  }
}

.contacts_btn__order {
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;

  background-color: $white-color;
  color: $button-and-heading-color;

  width: 161.45px;
  height: 44px;
  border-radius: 22px;

  margin-bottom: 113px;
}

.contacts_btn__offers:hover,
.contacts_btn__offers:focus {
  color: $white-color;
  background-color: $button-hover;
}

.contacts_btn__order:hover,
.contacts_btn__order:focus {
  background-color: $button-hover-two;
  color: $button-and-heading-color;
}

.contacts_btn__icon_red {
  margin-left: 13px;
  fill: $white-color;
  width: 8px;
  height: 10px;
}

.contacts_btn__icon_white {
  margin-left: 13px;
  fill: $button-and-heading-color;
  width: 8px;
  height: 10px;
}
