.footer {
  background-color: $primary-background-color;
  position: relative;
  padding-bottom: 43px;
}

.footer_titer {
  width: 234px;
  height: 135px;
  margin-bottom: 43px;
  @media screen and (min-width: 768px) {
    width: 340px;
    height: 183px;
  }
  @media screen and (min-width: 1200px) {
    width: 474px;
    height: 115px;
  }
}

.footer_titer__ice {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  color: $text-color;

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 30px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 34px;
  }
}

.footer_titer__text {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 39px;
  letter-spacing: 0.04em;
  color: $button-and-heading-color;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 55px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 58px;
    line-height: 66px;
  }
}

.footer_social__contacts {
  text-align: end;
}

.footer_social_list__item:not(:last-child) {
  margin-right: 30px;
}

.footer_social_list__icon {
  color: $heading-back-color-contacts-thee;
}

.footer_social_list__icon {
  fill: currentColor;
}

.footer_social_list__icon:hover,
.footer_social_list__icon:focus {
  color: $button-and-heading-color;
}

.footer_titer__icon {
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
  }
  @media screen and (min-width: 1200px) {
    padding-left: 114px;
    padding-right: 50px;
  }
}

.footer_social__list {
  display: flex;
  justify-content: flex-end;
  padding-bottom: 47px;
}
.footer_social__tell {
  font-weight: bold;
  font-size: 12px;
  margin-bottom: 112px;
  line-height: 1.86;
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
  }
}

.footer_tell {
  color: $button-and-heading-color;
}

.footer_social__copy_text {
  bottom: 0px;
  border-top: 1px solid #e1e1e1;
  position: absolute;
  width: 100%;
  padding-top: 22px;
  margin: 0 auto;
  left: 0;
  text-align: center;
  padding-bottom: 25px;
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
  }
}

.footer_social__copy {
  font-weight: bold;
  font-size: 14px;
  text-align: center;
  color: $primary-text-color;
  margin-top: 0%;
  line-height: 1.52;
  @media screen and (min-width: 768px) {
    line-height: 1.86;
  }
}

.footer_social__all {
  font-weight: bold;
  font-size: 14px;
  text-align: center;
  color: $primary-text-color;
  margin-top: 0%;
  line-height: 1.52;
  @media screen and (min-width: 768px) {
    padding-left: 3px;
    line-height: 1.86;
  }
}
